







import {Component, Prop, Vue} from "vue-property-decorator";
import {IVoice} from '@/common/interface';

// interface Item {
//     id: number
//     avatar: string
//     sound: string
//     created_at: string
//     updated_at: string,
// }

@Component({
    components: {}
})

export default class Voice extends Vue {

    isPlay = false

    @Prop({default: null}) item!: IVoice;

    created(): void {
        this.isPlay = true
        setTimeout(() => {
            this.isPlay = false
        }, 1000)
    }

    get avatar() {
        return require(`@/assets/images/${this.item.avatar}`)
    }

    get sound(): string {
        return `/${this.item.sound}`
    }

    toggle() {
        const audio: any = this.$refs.audio
        console.log(audio)
        if (this.isPlay) {
            audio.pause()
        } else {
            audio.play()
        }
        this.isPlay = !this.isPlay
    }

    ended() {
        this.isPlay = false
    }

}

