
























import Spinner from "@/components/Spinner.vue";

declare const MediaRecorder: any;

import {Component, Prop, Vue} from "vue-property-decorator";

@Component({
    components: {Spinner}
})
export default class Record extends Vue {
    status = "init";
    recorder: any = null;
    audioData: Blob[] = [];
    audioExtension: any = "";
    isOpen = false;
    isRecording = false;


    created(): void {
        navigator.mediaDevices.getUserMedia({audio: true}).then(stream => {
            this.recorder = new MediaRecorder(stream);
            this.recorder.addEventListener("dataavailable", (e: any) => {
                this.audioData.push(e.data);
                this.audioExtension = this.getExtension(e.data.type);
            });
            this.recorder.addEventListener("stop", () => {
                const audioBlob = new Blob(this.audioData);
                this.save(audioBlob)
            });
            this.status = "ready";
        });
    }

    save(blobData: any) {
        const data = new FormData();
        data.append('sound', blobData, 'sound.webm');
        data.append('avatar', `avatar_${Math.floor(Math.random() * 33 + 1)}.png`);

        this.axios.post('/api/record', data, {
            headers: {'content-type': 'multipart/form-data'}
        }).then((res: any) => {
            this.$emit('update', res.data)
            console.log('s')

        }).catch((error: any) => {
            new Error(error)
            console.log('e')
        });
    }

    toggleModal() {
        if(this.isRecording){
            this.stopRecording()
            return
        }
        this.isOpen = !this.isOpen
    }

    startRecording() {
        this.isRecording = true;
        this.audioData = [];
        this.recorder.start();
    }

    stopRecording() {
        this.isRecording = false;
        this.isOpen = false
        this.recorder.stop();
    }

    getExtension(audioType: string) {
        let extension = "wav";
        const matches = audioType.match(/audio\/([^;]+)/);

        if (matches) {
            extension = matches[1];
        }

        return "." + extension;
    }

    get recWrapClass(): string {
        return this.isOpen ? 'open' : 'close'
    }
}
